import {connect} from "react-redux";

@connect(
    state=>{
        return {
            id:state.open.id,
            ListenMusicLiObj:state.ListenMusicLiObj
        }
    }
)
class SongSheetLi extends React.Component{
    add=(ids,indexs)=>{

        this.props.dispatch(Lyric({url:"http://139.9.114.198:4000/lyric?id="+ids,method:"get",fu:()=>{
            console.log(ids,indexs)
            console.log(this.props.ListenMusicLiObj)
            var ListenMusicLiObj =  this.props.ListenMusicLiObj.map((item,i)=>{
                if(i==indexs){
                  item.fig = true;
                  // console.log(item)
                }else{
                  item.fig = false;
                }
                return item
              })
              // console.log(ListenMusicLiObj)
              this.props.dispatch(ListenMusicLiObjs(ListenMusicLiObj))
              
            // console.log(this.props)
            // console.log(id)
            this.props.dispatch(id(ids))
            history.push(`/player?id=${ids}`)
            
        }}))


    }
    render(){
        const {
            ListenMusicLiObj
        } = this.props
        
        return(
            <WingBlank>
                <div className="SongSheetLi" style={{height:"100vh"}}>
                    <ul>
                        {
                            ListenMusicLiObj.map((item,index)=>{
                                return(
                                    <li key={index} onClick={()=>this.add(item.id&&item.id,index)} className="moveIn">
                                    <div className="ListenMusicLi-age">{index+1}</div>
                                    <div className="ListenMusicLi-name">
                                <span>{item.name&&item.al.name}</span>
                                <span>{item.ar[0]&&item.ar[0].name} {item.al[0]&&item.al[0].name}</span>
                                    </div>
                                    <div className="ListenMusicLi-frequencys">
                                        <span className="iconfont icon-SanMiAppoutlinei17
"></span>
                                        <span className="iconfont icon-SanMiAppoutlinei18
"></span>
                                    </div>
                                </li>
                                )
                            })
                        }


                    </ul>
                </div>

            </WingBlank>
        )
    }
}
export default  SongSheetLi