import React,{Component} from "react";
import Head from "../../components/head"
import {WingBlank,Tabs} from 'antd-mobile';
import ListenMusicLi from "../../components/ListenMusicLi"
import { axios } from "../../../utils";
export default class ListenMusic extends React.Component{
    state = {
        ListenMusicLiObj : [],
        ListenMusicLiObjs : [],
    }
    componentDidMount(){
        axios({
            url:"http://139.9.114.198:4000/user/record?uid=2055175457&type=1",
            method:"GET"
        }).then(list=>{
            // console.log(list.data)
            this.setState({
                ListenMusicLiObj :　list.data.weekData
            })
        })
        axios({
            url:"http://139.9.114.198:4000/user/record?uid=2055175457&type=0",
            method:"GET"
        }).then(list=>{
            // console.log(list.data)
            this.setState({
                ListenMusicLiObjs :　list.data.allData
            })
        })
    }

    render(){
        const tabs2 = [
            { title: '最近一周', sub: '1' },
            { title: '所有时间', sub: '2' },
           
          ];
        return(
            <div className="ListenMusic" style={{height:"100vh"}}>
                <Head title="谁谁谁的听歌[排行榜"></Head>
                <WingBlank>
                <div>
                        <Tabs tabs={tabs2}
                        initialPage={1}
                        onChange={(tab, index) => { console.log('onChange', index, tab); }}
                        onTabClick={(tab, index) => { console.log('onTabClick', index, tab); }}
                        >
                        <ListenMusicLi　ListenMusicLiObj={this.state.ListenMusicLiObj}></ListenMusicLi>
                        <ListenMusicLi　ListenMusicLiObj={this.state.ListenMusicLiObjs}></ListenMusicLi>
                        <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '150px', backgroundColor: '#fff' }}>
                            Content of third tab
                        </div>
                        </Tabs>
                    </div>
            </WingBlank>
            </div>
        )
    }
}