import React,{Component} from "react";
import Head from "../../components/head"
import {WingBlank} from 'antd-mobile';
import "./index.scss"
import { history } from "../../../utils";



export default class ListenMusicLi extends React.Component{
    add=(id)=>{
        console.log(id)
        
        history.push(`/player?id=${id}`)
    }
    render(){
        const {
            ListenMusicLiObj
        } = this.props
        
        return(
            <WingBlank>
                <div className="ListenMusicLi" style={{height:"100vh"}}>
                    <ul>
                        {
                            ListenMusicLiObj.map((item,index)=>{
                                return(
                                    <li key={index} onClick={()=>this.add(item.song&&item.song.id)}>
                                    <div className="ListenMusicLi-age">{index+1}</div>
                                    <div className="ListenMusicLi-name">
                                <span>{item.song&&item.song.name}</span>
                                <span>{item.song&&item.song.alia}</span>
                                    </div>
                                    <div className="ListenMusicLi-frequency">
                                        <span className="iconfont icon-SanMiAppoutlinei5
        "></span>
                                <span>{item.song&&item.song.no}次</span>
                                    </div>
                                </li>
                                )
                            })
                        }


                    </ul>
                </div>
            </WingBlank>
        )
    }
}