import React,{Component} from "react";
import { WingBlank, WhiteSpace } from 'antd-mobile';
import "./index.scss"
import { axios } from "../../../utils";
import Playere from "../../../utils/Player/index"
import {connect} from "react-redux"

import { url ,urls , open , id ,Lyric , Lyrics , indexsarr , interval , LyricTimesArr ,  index , ListenMusicLiObjs , SongDate ,sourcePlay , totalTime , totalMinute ,totalTimeAlways , totalMinuteAlways , sourceAlways , formatAlways} from "../../redux/actions/index";
   /* 
                totalTime:0,  //当前音频总秒数
                totalMinute:0,//当前音频总分钟
                totalTimeAlways:0, //当前音频正在播放秒数
                totalMinuteAlways:0,//当前音频正在播放分钟
                */

var source  = null 

@connect(
    // 输入逻辑 mapStateToProps
    state=>{
        // console.log(state);
        return {
            ListenMusicLiObj:state.ListenMusicLiObj,
            open:state.open.open,
            url:state.open.url,
            id:state.open.id,    //当前播放id
            index:state.open.index, //当前播放下标
            SongDate:state.open.SongDate, //当前播放下标
            sourcePlay:state.open.sourcePlay, //当前是否播放
            formatAlways:state.open.formatAlways, //当前是否播放
            sourceAlways:state.open.sourceAlways, //当前是否播放
            totalTime:state.open.totalTime, //当前是否播放
            // ...state,
            // value:state.data
        }
    },
    // 输出逻辑 mapDispatchToProps
    // (dispatch)=>{
    //     return {
    //         // countadd:()=>dispatch(countAdd()),
    //         // countDesc:()=>dispatch(countDesc),
    //         // change:num=>dispatch(changeCount(num)),
    //         // changeCity:city=>dispatch(changeCity(city))
    //     }
    // }
)
  class PlayerDate extends React.Component{
    state = {
        url:localStorage.url,
        source:source,
        sourcePlay:this.props.sourcePlay,//播放
        picUrl:'',
        totalTime:0,
    }
    // PlayerDateSpeedDate=(e)=>{
    //     console.log(e.target.clientX)
    // }
    componentDidMount(){

        document.querySelector(".PlayerDate-center-center-Speed").onclick=(e)=>{
            var PlayerDate = document.querySelector(".PlayerDate-center-center-Speed").offsetLeft
            // console.log(e.pageX-PlayerDate)

            // console.log(document.querySelector(".PlayerDate-center-center-Speed").clientWidth)
            var pagex = e.pageX-PlayerDate;
         //    console.log(pagex)
         //    console.log((pagex/document.querySelector(".PlayerDate-center-center-Speed").clientWidth)*230)
            if(document.querySelector(".PlayerDate-center-center-Speed").clientWidth>=pagex){
             this.source&& (this.source.currentTime  =(pagex/document.querySelector(".PlayerDate-center-center-Speed").clientWidth)*this.state.totalTime)
                document.querySelector(".PlayerDate-center-center-SpeedDate").style.width = ((pagex)/100)*2+"rem"      
            }

        }









        // document.querySelector(".PlayerDate-center-center-SpeedCircular")
        // let  ev = new Event("mousedown");
        // document.querySelector(".PlayerDate-center-center-SpeedCircular").dispatchEvent(ev);

        // // document.onclick()
        // document.querySelector(".PlayerDate-center-center-SpeedCircular").addEventListener("mousedown",(e)=>{
        //     console.log(e.offsetX) //鼠标相对于事件源元素
        //     console.log(e.pageX) //鼠标相对于浏览器
        //     var offsetX = e.offsetX;
        //     var pageX = e.pageX;
        //     var PlayerDate = document.querySelector(".PlayerDate-center-center-Speed").offsetLeft
        //     this.source&&this.source.pause()
        //     // console.dir(document.querySelector(".PlayerDate-center-center-Speed").offsetLeft ) //父级距离左边视口的距离
        //     document.addEventListener("touchmove",(e)=>{
        //         this.source&&this.source.pause()
        //         // console.log(e.changedTouches[0].pageX)
        //         // console.log((PlayerDate/100)*2)
        //         var pageXS = e.changedTouches[0].pageX;
        //         // console.log(((pageXS-offsetX-PlayerDate)/100)*2)
        //         // if((PlayerDate/100)*2>((pageXS-offsetX-PlayerDate)/100)*2){
        //             // console.log(true)
        //             document.querySelector(".PlayerDate-center-center-SpeedDate").style.width = ((pageXS-offsetX-PlayerDate)/100)*2+"rem"
        //         // }
        //         // console.log(e.offsetX) //鼠标相对于事件源元素
        //         // console.log(e.pageX) //鼠标相对于事件源元素
        //     })
        // })
        document.addEventListener("touchend",(e)=>{
            document.removeEventListener('touchmove', this.touchmoves)
            console.log("松开")  
            this.source&&this.source.play() 
            
            // target.onmouseup = null;      
        })
        // document.addEventListener("touchstart",(e)=>{
        //     console.log(e)
        // })
            // document.addEventListener("touchmove",()=>{
            //     console.log(1)
            // })
        // document.onmousemove = (e)=>{
        //     console.log(e)
        // }
        // this.source.ondurationchange = ()=>{
        //     console.log(1)
        // }
        // console.log(this.props.url)
        // this.source.currentTime  =56
        this.source.addEventListener("timeupdate",()=>{
            
            if(this.source){
               this.props.dispatch(sourcePlay(true))
               
               
                // myEvent.emit("changecountone",80);
                
                // console.log(Math.floor(this.source.duration/60)) //分
                // console.log(Math.floor(this.source.duration%60)) //秒
                // console.log(('0'+`${Math.floor(this.source.duration/60)}:${Math.floor(this.source.duration%60)<10 ? '0'+Math.floor(this.source.duration%60) : Math.floor(this.source.duration%60)}`))
                this.props.dispatch(sourceAlways('0'+`${Math.floor(this.source.duration/60)}:${Math.floor(this.source.duration%60)<10 ? '0'+Math.floor(this.source.duration%60) : Math.floor(this.source.duration%60)}`))
               /* 
                totalTime:0,  //当前音频总秒数
                totalMinute:0,//当前音频总分钟
                totalTimeAlways:0, //当前音频正在播放秒数
                totalMinuteAlways:0,//当前音频正在播放分钟
                formatAlways:"00:00", //当前正在播放时间
                sourceAlways:"00:00", //当前总时间
                */
               this.props.dispatch(totalMinute(Math.floor(this.source.duration/60)))
               this.props.dispatch(totalTime(Math.floor(this.source.duration%60)))

                this.setState({
                    totalTime:this.source.duration
                })

                var timeDisplay;
                //用秒数来显示当前播放进度
                timeDisplay = Math.floor(this.source.currentTime);
                // console.log(Math.floor(this.source.currentTime))
                // console.log(this.source.currentTime.toFixed(3))
                this.props.suber(this.source.currentTime.toFixed(3))

                this.props.event(this.source.currentTime.toFixed(3))



                this.props.dispatch(totalTimeAlways(Math.floor(this.source.currentTime%60)))
                this.props.dispatch(totalMinuteAlways(Math.floor(this.source.currentTime/60)))

        

                var second = this.source.currentTime<60 ? (this.source.currentTime<10? '0'+Math.floor(this.source.currentTime) :Math.floor(this.source.currentTime) ) : (Math.floor(this.source.currentTime%60)<10?"0"+Math.floor(this.source.currentTime%60):Math.floor(this.source.currentTime%60));
                var branch = this.source.currentTime>60 ? (Math.floor(this.source.currentTime/60)<10 ? "0"+Math.floor(this.source.currentTime/60) : Math.floor(this.source.currentTime/60)) : '00'
                // console.log(`当前时间${branch}:${second}`)

                // console.dir(document.querySelector(".PlayerDate-center-center"))
                // console.log((document.querySelector(".PlayerDate-center-center").clientWidth)*(timeDisplay/(this.source.duration)))
                document.querySelector(".PlayerDate-center-center-SpeedDate").style.width = (((document.querySelector(".PlayerDate-center-center").clientWidth)*(timeDisplay/(this.source.duration))/100)*2)+"rem"

                this.props.dispatch(formatAlways(`${branch}:${second}`))
                //当视频播放到 4s的时候做处理
                if(timeDisplay == 4){
                        //处理代码
                }
            }

        },false); 
  

    //    console.log(this.props.data)
        this.setState({
            source : document.querySelector("source")
        })

        this.props.dispatch(url({ url:`http://139.9.114.198:4000/song/url?id=${window.location.hash.split("=")[1]}`,
        method:"get"}))
        
        if(!!this.props.SongDate.id){
            this.props.picUrl(this.props.SongDate.al.picUrl)
            // console.log("进入")
            this.props.title(this.props.SongDate.al.name)
        }else{
            if(window.location.hash.split("=")[1]){
                axios({
                    url:`http://139.9.114.198:4000/song/url?id=${window.location.hash.split("=")[1]}`,
                    method:"get"
                }).then(list=>{
                    localStorage.url = list.data.data[0].url
                    this.setState({
                        url:list.data.data[0].url,
                    })
                    axios({
                        url:`http://139.9.114.198:4000/song/detail?ids=${list.data.data[0].id}`,
                        method:"get"
                    }).then(list=>{
                        // console.log(list.data.songs[0].al)
                      
                       this.props.picUrl(list.data.songs[0].al.picUrl)
                       this.props.title(list.data.songs[0].al.name)
                    // console.log(this.props)
                    })
                })
    
            }
        }
       
        
    }
    play=()=>{
        this.props.Thisearly()
        // var obj = new Playere(this.state.source);
        // console.log(obj.plays)
        // console.log()
        this.setState({
            sourcePlay:!this.state.sourcePlay
        })
        this.props.dispatch(sourcePlay(!this.props.sourcePlay))
        
        if(this.state.sourcePlay){
            this.source.pause()
        }else{
            this.source.play()
        }
        
        // obj.plays()
    }
    SanMiAppoutlinei=()=>{
        this.setState({
            sourcePlay:true
        })
        this.props.dispatch(sourcePlay(true))
        
        if(this.state.sourcePlay){
            this.source.pause()
        }else{
            this.source.play()
        }
        // console.log("下一首")
        // console.log(this.props.ListenMusicLiObj.length)
        // console.log(this.props.index)
        // console.log(this.props.ListenMusicLiObj)
        // 
        var indexs = this.props.index
        ++indexs
       
        // this.props.dispatch(index(this.props.ListenMusicLiObj[index]))
        // this.props.dispatch(id(this.props.ListenMusicLiObj[this.props.index+1].id))
        if(indexs<this.props.ListenMusicLiObj.length){
            this.props.dispatch(Lyric({url:"http://139.9.114.198:4000/lyric?id="+this.props.ListenMusicLiObj[indexs].id,method:"get",fu:(value)=>{
                
                var LyricsTo = value.split(/\[\d*\:\d*\.\d*\]/);
                
                var LyricsArr = [];
       
                LyricsTo = LyricsTo.map((item,i)=>{
                        var LyricsObj = {}
                        LyricsObj.name = item;
                        LyricsObj.fig = false;
                        LyricsArr.push(LyricsObj);
                        return LyricsObj
                })

                var LyricTime =  value.split("\n")
                var LyricTimes = LyricTime.map((item,i)=>{
                    return item.match(/\[\d*\:\d*\.\d*\]/)
                })
                var LyricTimesArrs = []
                LyricTimes = LyricTimes.filter((item,i)=>{
                    if(LyricTimes[i]){
                        LyricTimesArrs.push(LyricTimes[i][0])
                    }else{
                        LyricTimesArrs.push('[00:00.000]')
                    }
                })
                console.log(LyricTimesArrs)
                this.props.dispatch(LyricTimesArr(LyricTimesArrs))

                console.log(LyricsArr)
                this.props.dispatch(Lyrics(LyricsArr))
                this.props.LyricsToArr(LyricsArr)
                this.props.myEvent.emit("LyricsToArr",LyricsArr)
                this.props.dispatch(indexsarr(-1))
                
                this.props.dispatch(interval(3))
                this.props.dispatch(SongDate(this.props.ListenMusicLiObj[indexs]))
                this.props.dispatch(index(indexs))
                this.props.dispatch(id(this.props.ListenMusicLiObj[indexs].id))


                
                this.props.dispatch(Lyric({url:"http://139.9.114.198:4000/lyric?id="+this.props.ListenMusicLiObj[indexs].id,method:"get",fu:(value)=>{
                     var LyricsTo = value.split(/\[\d*\:\d*\.\d*\]/);
                    var LyricsArr = [];
           
                    LyricsTo = LyricsTo.map((item,i)=>{
                         var LyricsObj = {}
                         LyricsObj.name = item;
                         LyricsObj.fig = false;
                         LyricsArr.push(LyricsObj);
                         return LyricsObj
                    })
                    this.props.dispatch(Lyrics(LyricsTo))
                    this.props.dispatch(url({ url:`http://139.9.114.198:4000/song/url?id=${this.props.ListenMusicLiObj[indexs].id}`,
                method:"get",fu:()=>{
                    
                    // var LyricsTo = add.split(/\[\d*\:\d*\.\d*\]/);
                    // var LyricsArr = [];
           
                    // LyricsTo = LyricsTo.map((item,i)=>{
                    //      var LyricsObj = {}
                    //      LyricsObj.name = item;
                    //      LyricsObj.fig = false;
                    //      LyricsArr.push(LyricsObj);
                    //      return LyricsObj
                    // })
                    // this.props.dispatch(Lyrics(LyricsTo))
    
                 var ListenMusicLiObj =  this.props.ListenMusicLiObj.map((item,i)=>{
                   if(indexs==i){
                     item.fig = true;
                     // console.log(item)
                   }else{
                     item.fig = false;
                   }
                   return item
                 })
                 // console.log(ListenMusicLiObj)
                 this.props.dispatch(ListenMusicLiObjs(ListenMusicLiObj))
                }}))
                }}))
            }}))




           
        }

    }
    SanMiAppoutlinei2=()=>{
        // console.log("上一首")
        this.setState({
            sourcePlay:true
        })
        this.props.dispatch(sourcePlay(true))
        
        if(this.state.sourcePlay){
            this.source.pause()
        }else{
            this.source.play()
        }

        var indexs = this.props.index;
        --indexs
        
        if(indexs>=0 && this.props.ListenMusicLiObj[indexs]){

            this.props.dispatch(Lyric({url:"http://139.9.114.198:4000/lyric?id="+this.props.ListenMusicLiObj[indexs].id,method:"get",fu:(value)=>{
                
                var LyricsTo = value.split(/\[\d*\:\d*\.\d*\]/);
                
                var LyricsArr = [];
       
                LyricsTo = LyricsTo.map((item,i)=>{
                        var LyricsObj = {}
                        LyricsObj.name = item;
                        LyricsObj.fig = false;
                        LyricsArr.push(LyricsObj);
                        return LyricsObj
                })


                var LyricTime =  value.split("\n")
                var LyricTimes = LyricTime.map((item,i)=>{
                    return item.match(/\[\d*\:\d*\.\d*\]/)
                })
                var LyricTimesArrs = []
                LyricTimes = LyricTimes.filter((item,i)=>{
                    if(LyricTimes[i]){
                        LyricTimesArrs.push(LyricTimes[i][0])
                    }else{
                        LyricTimesArrs.push('[00:00.000]')
                    }
                })
                
                this.props.dispatch(LyricTimesArr(LyricTimesArrs))
                
                console.log(LyricsArr)
                this.props.dispatch(Lyrics(LyricsArr))
                this.props.LyricsToArr(LyricsArr)
                this.props.myEvent.emit("LyricsToArr",LyricsArr)
                this.props.dispatch(indexsarr(-1))

                this.props.dispatch(interval(3))
                this.props.dispatch(SongDate(this.props.ListenMusicLiObj[indexs]))
                this.props.dispatch(index(indexs))
                this.props.dispatch(id(this.props.ListenMusicLiObj[indexs].id))
                



                this.props.dispatch(url({ url:`http://139.9.114.198:4000/song/url?id=${this.props.ListenMusicLiObj[indexs].id}`,
                method:"get",fu:()=>{
                 var ListenMusicLiObj =  this.props.ListenMusicLiObj.map((item,i)=>{
                   if(indexs==i){
                     item.fig = true;
                     // console.log(item)
                   }else{
                     item.fig = false;
                   }
                   return item
                 })
                 // console.log(ListenMusicLiObj)
                 this.props.dispatch(ListenMusicLiObjs(ListenMusicLiObj))
                }}))
            }}))


           
        }

    }

    touchmoves=(event)=>{
        var PlayerDate = document.querySelector(".PlayerDate-center-center-Speed").offsetLeft
        // console.log("PlayerDate"+PlayerDate)
    //    console.log(event.changedTouches[0].pageX)
    // clientWidth
    console.log(document.querySelector(".PlayerDate-center-center-Speed").clientWidth)
       var pagex = event.changedTouches[0].pageX - PlayerDate;
    //    console.log(pagex)
    //    console.log((pagex/document.querySelector(".PlayerDate-center-center-Speed").clientWidth)*230)
       if(document.querySelector(".PlayerDate-center-center-Speed").clientWidth>=pagex){
        this.source&& (this.source.currentTime  =(pagex/document.querySelector(".PlayerDate-center-center-Speed").clientWidth)*this.state.totalTime)
           document.querySelector(".PlayerDate-center-center-SpeedDate").style.width = ((pagex)/100)*2+"rem"      
       }
    }
    

    SpeedCircular=(e)=>{
        this.source.pause()
        // console.log(e.target)
        // console.log(e.target.getBoundingClientRect().left)
        var left = e.target.getBoundingClientRect().left
        // console.log(left)
        // console.log(e)
        // var target = e.target;
        // var disX = e.pageX - target.offsetLeft;
        // var disY = e.pageY - target.offsetTop;
        // console.log(e.target.pageX)
      
        document.addEventListener("touchmove",this.touchmoves)

        document.addEventListener("touchend",(e)=>{
            document.removeEventListener('touchmove', this.touchmoves)
            console.log("松开")  
            this.source&&this.source.play() 
            document.onmousemove = null;
            // target.onmouseup = null;      
        })
    }


    render(){
        return(
                <div className="PlayerDate">
                <WingBlank>
                    <div className="PlayerDate-top">
                        <span className="iconfont icon-xinaixin red"></span>
                        <span className="iconfont icon-xiazai1"></span>
                        <span className="iconfont icon-yptc_h"></span>
                        <span className="iconfont icon-liuyan">
                            
                            <div>
                                1w+
                            </div>
                        </span>
                        <span className="iconfont icon-dots-verticle
"></span>
                    </div>
                    <div className="PlayerDate-center">
        <div className="PlayerDate-center-top">{this.props.formatAlways}</div>
                        <div className="PlayerDate-center-center">
                            <div className="PlayerDate-center-center-Speed">
                                 <div className="PlayerDate-center-center-SpeedDate"  >
                                     <div className="PlayerDate-center-center-SpeedCircular" ref={el=>this.SpeedCirculars=el} onTouchStart={(e)=>this.SpeedCircular(e)}></div>
                                 </div>
                            </div>
                        </div>
                        <div className="PlayerDate-center-right">{this.props.sourceAlways}</div>
                    </div>
                    
                    <div className="PlayerDate-bottom">
                        <div className="PlayerDate-bottom-left">
                            <span className="iconfont icon-SanMiAppoutlinei4
"></span>
                        </div>
                        <div className="PlayerDate-bottom-center">
                            <span className="iconfont icon-SanMiAppoutlinei2
" onClick={this.SanMiAppoutlinei2}></span>
                            <span style={{display:!this.state.sourcePlay&&"none"}} className="iconfont icon-execute-" onClick={this.play}></span>
                            <span style={{display:this.state.sourcePlay&&"none",    fontSize: '.97rem'}}   onClick={this.play} className="iconfont icon-SanMiAppoutlinei17 
"></span>

                            <span className="iconfont icon-SanMiAppoutlinei" onClick={this.SanMiAppoutlinei}></span>
                        </div>
                        <div className="PlayerDate-bottom-button">
                            <span className="iconfont icon-SanMiAppoutlinei3
" onClick={()=>this.props.dispatch(open())}></span>
                        </div>
                    </div>
                </WingBlank>
              
                {/* <div className="SongSheet-Thisearly" style={{background: `url("${this.state.picUrl}") center center`,
    filter: 'blur(100px)'}}></div> */}
                 {/* <audio  autoplay="autoplay" ref={el=>this.source=el}>
                     <source src={this.props.url}  type="audio/mpeg" />
                </audio> */}

                <video src={this.props.url} ref={el=>this.source=el} autoplay="autoplay">
                您的浏览器不支持 video 标签。
                </video>
                </div>
        )
    }
    componentDidUpdate(){
        // console.log(this.props)
    //    console.log()
    // console.log(localStorage.url)
    // this.props.dispatch(urls(localStorage.url))
    //    if(!this.state.sourcePlay){
    //     this.source.pause()
    //     }else{
    //         this.source.play()
    //     }
    if(this.props.SongDate.id){
        this.props.picUrl(this.props.SongDate.al.picUrl)
        // console.log("进入")
        this.props.title(this.props.SongDate.al.name)
    }
   
    }
    componentWillUnmount(){
        console.log("销毁")
        document.removeEventListener('touchmove', this.touchmoves)
    }
}
export default PlayerDate