import React,{Component} from "react";
import { WingBlank, WhiteSpace } from 'antd-mobile';
import "./index.scss"
import { history } from "../../../utils";
import {LyricTimesArr,Lyric,LyricARRObj, Lyrics  , indexsarr,interval,url ,urls , open , id , index , ListenMusicLiObjs , SongDate ,sourcePlay , totalTime , totalMinute ,totalTimeAlways , totalMinuteAlways , sourceAlways , formatAlways} from "../../redux/actions";

import {connect} from "react-redux"
@connect(
    state=>{
        return {
            SongDate:state.open.SongDate,
            id:state.open.id
        }
    },

)
class SongSheetTop extends React.Component{
    historys = ()=>{
       this.props.dispatch(LyricTimesArr([]));
       this.props.dispatch(Lyrics([]));
       this.props.dispatch(indexsarr(-1));
       this.props.dispatch(interval(3));
       this.props.dispatch(LyricARRObj(""));
       setTimeout(()=>{
           history.go(-1)
       },100)
    }
    render(){
        const {
            title
        } = this.props
        
 
        return(
           
               <div className="SongSheetTop">
                   <div className="SongSheetTop-lfet" >
                       <span className="iconfont icon-houtui" onClick={this.historys}></span>
                       {title}
                   </div>
                   <div className="SongSheetTop-right">
                        <span className="iconfont icon-SanMiAppoutlinei7
"></span>
                        <span className="iconfont icon-SanMiAppoutlinei18
"></span>
                   </div>
               </div>
       
        )
    }
}
export default SongSheetTop