export const defaultState = []

export  const ListenMusicLiObj = (state=defaultState,action)=>{
    // console.log(111)
    // console.log(action)
    // console.log(state)
    switch(action.type){
        case "ListenMusicLiObj":
            // console.log("接收");
            // console.log(action.ListenMusicLiObj)
            return action.ListenMusicLiObj
            break;
        case "ListenMusicLiObjs":
            // console.log("接收");
            // console.log(action.ListenMusicLiObjs[0].fig)
            // console.log(action.ListenMusicLiObjs)
            return action.ListenMusicLiObjs
            break;
        case "removeLi":
            // console.log("接收");
            // console.log(action.removeLi[0].fig)
            // console.log(action.removeLi)
            return action.removeLi
            break;

        default:
        return state;
        break;
    }
}