import React,{Component} from "react";
import {Switch,Route,Redirect} from "react-router-dom";
import Mani from "./main";
import loginOne from "./login/loginOne";
import loginTwo from "./login/loginTwo";
import loginThree from "./login/loginThree";
import loginFour from "./login/loginFour";
import loginFive from "./login/loginFive";
import loginSix from "./login/loginSix";
import Personal from "./Personal"
import ListenMusic from "./ListenMusic"
import SongSheet from "./SongSheet"
import Player from "./Player";
import {Footplay} from "../components/Footplay"
import Photograph from "./Photograph"
export default class Layout extends React.Component{
    render(){
        return(
            <Switch>
                <Route path="/"  exact render={()=><Redirect to="/main/home"/>}></Route>
                <Route path="/main"  component={Mani}></Route>
                <Route path="/loginone"  component={loginOne} ></Route>
                <Route path="/logintwo"  component={loginTwo} ></Route>
                <Route path="/loginthree"  component={loginThree} ></Route>
                <Route path="/loginfour"  component={loginFour} ></Route>
                <Route path="/loginfive"  component={loginFive} ></Route>
                <Route path="/loginsix"  component={loginSix} ></Route>
                <Route path="/personal"  component={Personal} ></Route>
                <Route path="/listenMusic"  component={ListenMusic} ></Route>
                <Route path="/songsheet"  component={SongSheet} ></Route>
                <Route path="/player"  component={Player} ></Route>
                <Route path="/photograph"  component={Photograph} ></Route>
                {/* <Route path="/" component={()=>(<Redirect to="/mani"></Redirect>)} ></Route> */}
                

            </Switch>
        )
    }
}