import React,{Component} from "react";

import Manihead from  "../../../scripts/components/Manihead";
import Carousels from  "../../../scripts/components/Carousel";
import Everyday from  "../../../scripts/components/everyday";
import Recommend from  "../../../scripts/components/Recommend";
import RecommendSong from  "../../../scripts/components/RecommendSong";
import Footplay from  "../../../scripts/components/Footplay";
import PersonalHead from  "../../../scripts/components/PersonalHead";
import { axios } from "../../../utils";



export default class Home extends React.Component{
    state = {
        opens: false,
        RecommendSong : [],
        RecommendSongs : []
      }
      
      componentDidMount(){
        axios({
            url:"http://139.9.114.198:4000/recommend/resource",
            method:"get"
        }).then(list=>{
            console.log(list.data.recommend)
            this.setState({
                RecommendSong:list.data.recommend
            })
        })


    }

      onOpenChange = (...args) => {
        // console.log(args);
        this.setState({ opens: !this.state.opens });
      }
    render(){
        return (
            <div>
                
                <Carousels></Carousels>
                <Everyday></Everyday>
                <Recommend title={"推荐歌单"} name={"歌单广场"} url={"javascript:;"}></Recommend>
                <RecommendSong RecommendSong={this.state.RecommendSong}></RecommendSong>

                {/* <Footplay url="https://p2.music.126.net/VFd5cboNTbnYsWZ5DBn9bg==/18953381440004340.jpg" name = "正义使者"></Footplay> */}
            </div>
        )
    }
}