import React,{Component} from "react";
import { Drawer, List, NavBar, Icon , WingBlank} from 'antd-mobile';
import "./index.scss"
import { axios } from "../../../utils";
import {connect} from "react-redux"
import { open } from "../../redux/actions/index";
import img from "../../../assets/images/images/Screenshot_2019-11-29-11-29-07_03.jpg"
import img1 from "../../../assets/images/images/Screenshot_2019-11-29-11-29-07_05.jpg"
import img2 from "../../../assets/images/images/Screenshot_2019-11-29-11-29-07_07.jpg"
import img3 from "../../../assets/images/images/Screenshot_2019-11-29-11-29-07_12.jpg"
import img4 from "../../../assets/images/images/Screenshot_2019-11-29-11-29-07_13.jpg"
import {index,id,url,ListenMusicLiObjs , removeLi , SongDate ,Lyrics ,Lyric,interval,LyricTimesArr , indexsarr} from "../../redux/actions/index"

@connect(
    // 输入逻辑 mapStateToProps
    state=>{
        // console.log(state.open.open);
        return {
            open:state.open.open,
            index:state.open.index,
            ListenMusicLiObj:state.ListenMusicLiObj
            // ...state,
            // value:state.data
        }
    },
    // 输出逻辑 mapDispatchToProps
    // (dispatch)=>{
    //     return {
    //         ListenMusicLiObj:()=>dispatch(ListenMusicLiObj()),
    //         // countDesc:()=>dispatch(countDesc),
    //         // change:num=>dispatch(changeCount(num)),
    //         // changeCity:city=>dispatch(changeCity(city))
    //     }
    // }
)
class PlayerLi extends React.Component{
    state = {
      flg:false,//判断当前播放源,
    }
      onOpenChange = (...args) => {
       this.props.dispatch(open())
      //  console.log(1)
      }
    componentDidMount(){


    }
    add=(indexs,ids)=>{
      this.props.dispatch(Lyric({url:"http://139.9.114.198:4000/lyric?id="+this.props.ListenMusicLiObj[indexs].id,method:"get",fu:(value)=>{
                
          var LyricsTo = value.split(/\[\d*\:\d*\.\d*\]/);
          
          var LyricsArr = [];
 
          LyricsTo = LyricsTo.map((item,i)=>{
                  var LyricsObj = {}
                  LyricsObj.name = item;
                  LyricsObj.fig = false;
                  LyricsArr.push(LyricsObj);
                  return LyricsObj
          })

          var LyricTime =  value.split("\n")
          var LyricTimes = LyricTime.map((item,i)=>{
              return item.match(/\[\d*\:\d*\.\d*\]/)
          })
          var LyricTimesArrs = []
          LyricTimes = LyricTimes.filter((item,i)=>{
              if(LyricTimes[i]){
                  LyricTimesArrs.push(LyricTimes[i][0])
              }else{
                  LyricTimesArrs.push('[00:00.000]')
              }
          })
          console.log(LyricTimesArrs)
          this.props.dispatch(LyricTimesArr(LyricTimesArrs))

          console.log(LyricsArr)
          this.props.dispatch(Lyrics(LyricsArr))
          this.props.LyricsToArr(LyricsArr)
          this.props.myEvent.emit("LyricsToArr",LyricsArr)
          this.props.dispatch(indexsarr(-1))
          
          this.props.dispatch(interval(3))
          // this.props.dispatch(SongDate(this.props.ListenMusicLiObj[indexs]))
          // this.props.dispatch(index(indexs))
          // this.props.dispatch(id(this.props.ListenMusicLiObj[indexs].id))

        }}))

      // console.log("进入")
      console.log(this.props.ListenMusicLiObj[indexs])
      this.props.dispatch(SongDate(this.props.ListenMusicLiObj[indexs]))
      this.props.dispatch(index(indexs))
      this.props.dispatch(id(ids))
      this.props.dispatch(url({ url:`http://139.9.114.198:4000/song/url?id=${ids}`,
      method:"get"}))
      var ListenMusicLiObj =  this.props.ListenMusicLiObj.map((item,i)=>{
        if(i==indexs){
          item.fig = true;
          // console.log(item)
        }else{
          item.fig = false;
        }
        return item
      })
      this.props.dispatch(ListenMusicLiObjs(ListenMusicLiObj))

    }
    removeLi=(indexs,ids)=>{
      // console.log(indexs,ids)
      var ListenMusicLiObj =  this.props.ListenMusicLiObj.filter((item,i)=>{
        return i!==indexs
      })
      this.props.dispatch(removeLi(ListenMusicLiObj))
      // console.log("id",this.props.ListenMusicLiObj[indexs+1].id)
      
      this.props.dispatch(id(ids))
      // console.log(this.props.index>indexs)
      // console.log(this.props.index == indexs)
     console.log(this.props.index)
     console.log(indexs)
     if(indexs>this.props.index){

     }else{
      this.props.dispatch(index(indexs))
     }
     if(this.props.index == indexs){
       console.log("进入1111")
       this.props.dispatch(url({ url:`http://139.9.114.198:4000/song/url?id=${this.props.ListenMusicLiObj[indexs+1].id}`,
       method:"get",fu:()=>{
        
        



        // this.props.dispatch(SongDate(this.props.ListenMusicLiObj[indexs]))
         ListenMusicLiObj =  this.props.ListenMusicLiObj.map((item,i)=>{
          if(i+1==indexs+1){
            item.fig = true;
            // console.log(item)
          }else{
            item.fig = false;
          }
          return item
        })
        // console.log(ListenMusicLiObj)




        this.props.dispatch(ListenMusicLiObjs(ListenMusicLiObj))
       }}))

     }
      
    }
    render(){
        const sidebar = (
          <div className="PlayerLi">
              <div className="PlayerLi-top">
                <div className="PlayerLi-top-left">
                  <img src={img} alt=""/>
                  <span>列表循环(84)</span>
                </div>

                <div className="PlayerLi-top-right">
                  <div className="PlayerLi-top-right-left">
                  <img src={img1} alt=""/>
                  <span>收藏全部</span>
                  </div>
                  <div className="PlayerLi-top-right-right">
                    <img src={img2} alt=""/>
                  </div>
                </div>
              </div>
            
              <div className="PlayerLi-li">
                <ul>
                  {
                    this.props.ListenMusicLiObj.map((item,index)=>{
                      return (
                        <li key={index} className="moveIn" >
                        <div className="PlayerLi-top-li" >
                          <div className="PlayerLi-top-left" style={{width:"4rem"}} onClick={()=>this.add(index,item.id)}>
                            <img src={img3} alt="" style={{display:!item.fig&&"none"}}/>
                      <span className={item.fig &&'spanLyricPng'} style={{width:'5rem',overflow: 'hidden',textOverflow:'ellipsis',whiteSpace: 'nowrap' }}>{item.al.name}</span>
                          </div>
  
                          <div className="PlayerLi-top-right">
                            <div className="PlayerLi-top-right-left">
                            <img src='' alt=""/>
                            <span style={{display:!item.fig&&"none"}} className="spanPng">播放来源</span>
                            </div>
                            <div className="PlayerLi-top-right-right">
                              <img src={img4} alt="" onClick={()=>this.removeLi(index,item.id)}/>
                            </div>
                          </div>
                        </div>
                    </li>
                      )
                    })
                  }
                 
                </ul>
              </div>
          </div>

        );
        const {
            open
        } = this.props
      //  console.log(open)
        return(
          <WingBlank>
            <div className="SongSheetDate PlayerLis">
                <Drawer
                    position="bottom"
                    className="my-drawer"
                    style={{ }}
                    enableDragHandle
                    contentStyle={{ color: '#A6A6A6', textAlign: 'center', paddingTop: 42 }}
                    sidebar={sidebar}
                    open={open}
                    onOpenChange={this.onOpenChange}
                >
                </Drawer>
            </div>
          </WingBlank>
        )
    }
    componentDidUpdate(){
      // console.log(this.props)
      // console.log(this.props.ListenMusicLiObj[0].fig)
      // console.log()
    }
}
export default  PlayerLi