import React,{Component} from "react";
import "./index.scss"
import img from  "../../../../assets/images/Screenshot_2019-11-29-11-19-16_03.jpg";
import img1 from  "../../../../assets/images/Screenshot_2019-11-29-11-19-16_07.jpg";
import img2 from  "../../../../assets/images/Screenshot_2019-11-29-11-19-16_09.jpg";
import img3 from  "../../../../assets/images/Screenshot_2019-11-29-11-19-16_11.jpg";
import img4 from  "../../../../assets/images/Screenshot_2019-11-29-11-19-16_13.jpg";
import img5 from  "../../../../assets/images/Screenshot_2019-11-29-11-19-16_03 (2).jpg";

import { Modal, Button, WhiteSpace, WingBlank, Toast } from 'antd-mobile';
import { history } from "../../../../utils";
const alert = Modal.alert;


export default class loginOne extends React.Component{
    state={
        flg:false
    }
    add=(e)=>{
        this.setState({
            flg:!this.state.flg
        })
        if(this.state.flg){
            e.target.setAttribute('class',"")
        }else{
            e.target.setAttribute('class',"backg")
        }
        
    }
    adds=()=>{
        if(!this.state.flg){
            
            this.showAlert()
        }else{
            history.push("/logintwo")
            console.log(this.state.flg)
        }
    }
    showAlert = () => {
        const alertInstance = alert('请同意用户协议', 'Are you sure???', [
          { text: '取消', onPress: () => console.log('cancel'), style: 'default' },
          { text: '确定', onPress: () => this.setState({
            flg:true
        }) },
        ]);
        setTimeout(() => {
          // 可以调用close方法以在外部close
          console.log('auto close');
          alertInstance.close();
        }, 500000);
      };
    render(){
        // console.log(this.state.flg)
        return (
            <div className="loginOne">
                <div className="loginOne-img" style={{width:'1.46rem',height:'1.41rem',margin:"0 auto",marginTop:"2.75rem"}}>
                    <img src={img} alt="" style={{width:'1.46rem',height:'1.41rem'}}/>
                </div>
                <div  className="loginOne-logon" style={{width:"5.33rem",height:"3.21rem",margin:"0 auto" }}>
                     <div className="loginOne-Sign" onClick={this.adds}>
                         手机号登录
                     </div>
                     <ul>
                         <li>
                            <a href="javascript:;"> <img src={img1} alt=""/></a>
                         </li>
                         <li>
                            <a href="javascript:;"> <img src={img2} alt=""/></a>
                         </li>
                         <li>
                            <a href="javascript:;"> <img src={img3} alt=""/></a>
                         </li>
                         <li>
                            <a href="javascript:;"> <img src={img4} alt=""/></a>
                         </li>
                     </ul>
                     <div className="loginOne-button">
                         <span className={this.state.flg&&"backg"||''} onClick={this.add}>
                             
                         </span>
                        <i onClick={this.add}>
                            
                            同意
                        </i>
                        <span>
                            <a href="javascript:;">
                            用户协议
                            </a>
                        </span>
                        <span>
                            <a href="javascript:;">
                            隐私政策
                            </a>
                        </span>
                        <span>
                            <a href="javascript:;">
                            儿童策略
                            </a>
                        </span>

                       
                     </div>
                </div>
            </div>
        )
    }
}