import React,{Component} from "react";
import { Carousel, WingBlank } from 'antd-mobile';
import "./index.scss"

export default class Footplay extends React.Component{
  
      render() {
        // const url = "https://p2.music.126.net/VFd5cboNTbnYsWZ5DBn9bg==/18953381440004340.jpg"
        const paly = true;  //是否播放
        const palyButton = false;  //播放按钮
        const palyList = false; //音乐列表
        // const name = "正义使者";

        const {
          url,
          name
        } = this.props

        return (
          <WingBlank style={{display:!paly&&"none"}}>
           <div className="Footplay">
              <div className="Footplay-left">
                <div className="Footplay-left-left">
                  <img src={url} alt=""/>
                </div>
                <div className="Footplay-left-right">
        <span>{name}</span>
                </div>
              </div>
              <div className="Footplay-right">
                <div className="Footplay-right-left">
                  <span className="iconfont icon-SanMiAppoutlinei17
"></span>
                </div>
                <div className="Footplay-right-right">
                  <span className="iconfont icon-SanMiAppoutlinei12
"></span>
                </div>
              </div>
           </div>
          </WingBlank>
        );
      }
}