import React,{Component} from "react";
import { WingBlank, WhiteSpace } from 'antd-mobile';
import SongSheetLi from "../../components/SongSheetLi"
import "./index.scss"
import { axios } from "../../../utils";
import {connect} from "react-redux"
import { ListenMusicLiObj } from "../../redux/actions/index";


@connect(
    // 输入逻辑 mapStateToProps
    state=>{
        // console.log(state);
        return {
            ListenMusicLiObj:state.ListenMusicLiObj,
            // ...state,
            // value:state.data
        }
    },
    // 输出逻辑 mapDispatchToProps
    // (dispatch)=>{
    //     return {
    //         ListenMusicLiObj:()=>dispatch(ListenMusicLiObj()),
    //         // countDesc:()=>dispatch(countDesc),
    //         // change:num=>dispatch(changeCount(num)),
    //         // changeCity:city=>dispatch(changeCity(city))
    //     }
    // }
)
class SongSheetDate extends React.Component{
    state =  {
        ListenMusicLiObj: []
    }
    componentDidMount(){

        // console.log()
        this.props.dispatch(ListenMusicLiObj({
            url:`http://139.9.114.198:4000/playlist/detail?id=${window.location.hash.split("=")[1]}`,
            method:"GET"
        }))

     if(window.location.hash.split("=")[1]){
        // axios({
        //     url:`http://139.9.114.198:4000/playlist/detail?id=${window.location.hash.split("=")[1]}`,
        //     method:"GET"
        // }).then(list=>{
        //     console.log(list.data)
        //     this.setState({
        //         ListenMusicLiObj:list.data.playlist.tracks
        //     })
        // })
        this.props.dispatch(ListenMusicLiObj({
            url:`http://139.9.114.198:4000/playlist/detail?id=${window.location.hash.split("=")[1]}`,
            method:"GET"
        }))
     }else{

        axios({
            url:`http://139.9.114.198:4000/login/cellphone?phone=15827130537&password=xh13554217455`,
            method:"GET"
        }).then(list=>{
            axios({
                url:`http://139.9.114.198:4000/likelist?uid=2055175457`,
                method:"GET"
            }).then(list=>{
                axios({
                    url:`http://139.9.114.198:4000/song/detail?ids=${list.data.ids.join(",")}`,
                    method:"GET"
                }).then(list=>{
                    console.log(list.data.songs)
                    this.setState({
                        ListenMusicLiObj:list.data.songs
                    })
                })
            })
        })
     }

    }
    render(){
        const {
            id,
            ListenMusicLiObj
        } = this.props
       
        return(
            <div className="SongSheetDate" id={id}>
                <div className="SongSheetDate-button">
                    <span className="iconfont icon-SanMiAppoutlinei17
"></span>
                    <span>播放全部</span>
                    <span>(共1首)</span>
                </div>

                <SongSheetLi ListenMusicLiObj={ListenMusicLiObj}></SongSheetLi>
            </div>
        )
    }
}
export default  SongSheetDate