import { Drawer, List, NavBar, Icon } from 'antd-mobile';
import React,{Component} from "react";
import "./index.scss"
import img from "../../../../src/assets/images/images/123_03.jpg"
import img1 from "../../../../src/assets/images/images/123_14.jpg"
import PersonalHeadTag from "../PersonalHeadTag"
import PersonalHeadLI from "../PersonalHeadLI"
import { history, axios } from '../../../utils';
 class PersonalHead extends React.Component {
   state = {
     fig : true,
     name:'',
     avatarUrl:''
   }
  componentDidMount(){
    axios({
      url:"http://139.9.114.198:4000/login/status?&timestamp="+new Date().getTime(),
      method:"GET",
      params:{
        cookies:15827130537
      }
    }).then(list=>{
      // console.log(list.data)
      if(list.data.code == 200){
        this.setState({
          fig:true,
          name:list.data.profile.nickname,
          avatarUrl:list.data.profile.avatarUrl,
        })
      }
    }).catch(err=>{
      this.setState({
        fig:false
      })
    })
  }
  render() {
    // fix in codepen
    // console.log(this.state.fig)
    const sidebar = (<div style={{width:"6.68rem",padding:".3rem",height:"86%",overflow:"auto"}}>

                  {
                    this.state.fig && (<div className="PersonalHead-top" onClick={()=>history.push("/personal")} >
                    <div className="PersonalHead-top-left">
                      <img src={img} alt=""/>
                    </div>
                    <div className="PersonalHead-top-right" >
                      <span>昵称昵称昵称</span>
                      <i>Lv.0</i>
                      <b> <span></span> 签到</b>
                    </div>
                </div>) || ( <div className="PersonalHead-top PersonalHead-tops" >
                       <p style={{touchAction:"center" ,opacity:".6"}} >登录网易云音乐</p>
                       <p style={{touchAction:"center", opacity:".6"}}>手机电脑多端同步,尽享海量高品质音乐</p>
                       <div style={{width:"1.7rem",height:".5rem",textAlign:"center",marginTop:".2rem",background:"#cccccc",lineHeight:".5rem",borderRadius:".3rem",opacity:".6"}} onClick={()=>history.push("/loginone")}>立即登录</div>
                    </div>)
                  }
                    

                   

                    <div>
                      <img src={img1} alt="" style={{width:"5.9rem",height:"1.08rem"}}/>
                    </div>

                    <div className="PersonalHead-tag">
                        <PersonalHeadTag></PersonalHeadTag>
                    </div>

                    <div className="PersonalHead-button">
                      <PersonalHeadLI></PersonalHeadLI>
                    </div>
    </div>);
   const {
    open,
    onOpenChange
   } = this.props
    return (<div>
      
      <Drawer
        position="left"
        className="my-drawer"
        style={{ minHeight: document.documentElement.clientHeight }}
        enableDragHandle
        contentStyle={{ color: '#000000', textAlign: 'center', paddingTop: 42 }}
        sidebar={sidebar}
        open={open}
        onOpenChange={onOpenChange}
      >
      </Drawer>
    </div>);
  }
}

export default PersonalHead