import React,{Component} from "react";
import { WingBlank, WhiteSpace } from 'antd-mobile';
import "./index.scss"
import img from "../../../assets/images/images/Screenshot_2019-11-29-11-32-06_16.jpg"
import img1 from "../../../assets/images/images/Screenshot_2019-11-29-11-32-06_19.jpg"
import img2 from "../../../assets/images/images/Screenshot_2019-11-29-11-32-06_21.jpg"
import img3 from "../../../assets/images/images/Screenshot_2019-11-29-11-32-06_23.jpg"
import img4 from "../../../assets/images/images/Screenshot_2019-11-29-11-32-06_25.jpg"

var MyMusicobj = [
    {
        url:img,
        name:"本地音乐",
        path:"",
        num:0
    },{
        url:img,
        name:"最近播放",
        path:"",
        num:0
    },{
        url:img,
        name:"下载管理",
        path:"",
        num:0
    },{
        url:img,
        name:"我的电台",
        path:"",
        num:0
    },{
        url:img,
        name:"我的收藏",
        path:"",
        num:0
    },
]


export default class MyMusic extends React.Component{
    render(){
        return(
            <WingBlank>
  <div className="MyMusic">
               <ul>
                   {
                       MyMusicobj.map((item,index)=>{
                        return (
                            // <li onClick={()=>history.push(item.path)}>
                            <li>
                                <img src={item.url} alt=""/>
                                <div>
                                    <span>{item.name}</span>
                                    <span>({item.num})</span>
                                </div>
                            </li>
                        )
                       })
                   }
                  
               </ul>
           </div>
            </WingBlank>
         
        )
    }
}