import React,{Component} from "react";
import "./index.scss"
import { TabBar } from 'antd-mobile';
import img from "../../../assets/images/images/123_17.jpg"
import img1 from "../../../assets/images/images/123_19.jpg"
import img2 from "../../../assets/images/images/123_21.jpg"
import img3 from "../../../assets/images/images/123_23.jpg"
import { history } from "../../../utils";


const PersonalHeadTagObj = [
    {
        url:img,
        name:'拍照',
        path:"/photograph",
        selectedTab:"My"
    },{
        url:img1,
        name:'我的好友',
        path:"",
        selectedTab:"GoodFriend"
    },{
        url:img2,
        name:'听歌识曲',
        path:"",
        selectedTab:"Song"
    },{
        url:img3,
        name:'个性装扮',
        path:"",
        selectedTab:"DressUp"
    },
]

 class PersonalHeadTag extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
          selectedTab: 'redTab',
          hidden: false,
          fullScreen: false,
        };
      }
    
  render() {
    return (
            <div>
                <div style={this.state.fullScreen ? { position: 'fixed', height: '100%', width: '100%', top: 0 } : { height: 400 }}>
        <TabBar
          unselectedTintColor="#949494"
          tintColor="#33A3F4"
          barTintColor="white"
          hidden={this.state.hidden}
        >
          {
              
              PersonalHeadTagObj.map((item,index)=>{
                  return (
                        <TabBar.Item
                        key={index}
                        title={item.name}
                        key="Life"
                        className="moveIn"
                        icon={<div style={{
                        width: '22px',
                        height: '22px',
                        background: `url(${item.url}) center center /  21px 21px no-repeat` }}
                        />
                        }
                        selectedIcon={<div style={{
                            width: '22px',
                            height: '22px',
                            background: `url(${item.url}) center center /  21px 21px no-repeat` }}
                        />
                        }
                        selected={this.state.selectedTab === item.selectedTab}
                        badge={1}
                        onPress={() => {
                          history.push(item.path)
                        this.setState({
                            selectedTab: item.selectedTab,
                        });
                        }}
                        data-seed="logId"
                    >
                    </TabBar.Item>
                  )
              })
          }

        </TabBar>
      </div>
            </div>
    );
  }
}

export default PersonalHeadTag