export const defaultState = {
    open:false, //底部列表移除移入
    url:'',   //当前播放url
    id:0,    //当前播放id
    index:0, //当前播放下标
    flg:false,//判断当前播放源
    SongDate:{},
    sourcePlay:true,
    totalTime:0,  //当前音频总秒数
    totalMinute:0,//当前音频总分钟
    totalTimeAlways:0, //当前音频正在播放秒数
    totalMinuteAlways:0,//当前音频正在播放分钟
    formatAlways:"00:00", //当前正在播放时间
    sourceAlways:"00:00", //当前总时间

    Lyric:'',//歌词
    LyricTimesArr:[], //时间
    Lyrics:[], //歌词,
    indexs:-1,  //当前播放歌词下标
    interval:3 //间隔
}

export  const open = (state=defaultState,action)=>{
    // console.log(111)
    // console.log(action)
    // console.log(state)
    switch(action.type){
       case "open":
           return {...state,...{open:!state.open}};
            break;

       case "url":
        //    console.log(action.url)
           return {...state,...{url:action.url}};
            break;

       case "urls":
        //    console.log(action.urls)
           return {...state,...{url:action.urls}};
            break;
       case "id":
           return {...state,...{id:action.id}};
            break;
       case "index":
        //    console.log(action.index)
           return {...state,...{index:action.index}};
            break;
       case "SongDate":
        //    console.log(action.SongDate)
           return {...state,...{SongDate:action.SongDate}};
            break;
       case "sourcePlay":
        //    console.log(action.sourcePlay)
           return {...state,...{sourcePlay:action.sourcePlay}};
            break;
       case "totalTime":
        //    console.log(action.totalTime)
           return {...state,...{totalTime:action.totalTime}};
            break;
       case "totalTime":
        //    console.log(action.totalTime)
           return {...state,...{totalTime:action.totalTime}};
            break;
       case "totalTimeAlways":
        //    console.log(action.totalTimeAlways)
           return {...state,...{totalTimeAlways:action.totalTimeAlways}};
            break;
       case "totalMinuteAlways":
        //    console.log(action.totalMinuteAlways)
           return {...state,...{totalMinuteAlways:action.totalMinuteAlways}};
            break;
       case "formatAlways":
        //    console.log(action.formatAlways)
           return {...state,...{formatAlways:action.formatAlways}};
            break;
       case "sourceAlways":
        //    console.log(action.sourceAlways)
           return {...state,...{sourceAlways:action.sourceAlways}};
            break;
            /*     
               Lyric:'',//歌词
               LyricTimesArr:[], //时间
               Lyrics:[], //歌词,
               indexs:-1,  //当前播放歌词下标
               interval:3 //间隔 
            */
           case "Lyric":
               // console.log(action.Lyric)
               return {...state,...{Lyric:action.Lyric}};
                break;
           case "LyricTimesArr":
            //    console.log(action.LyricTimesArr)
               return {...state,...{LyricTimesArr:action.LyricTimesArr}};
                break;
           case "Lyrics":
               // console.log(action.Lyrics)
               return {...state,...{Lyrics:action.Lyrics}};
                break;
                case "indexs":
                     // console.log(action.indexs)
                     return {...state,...{indexs:action.indexs}};
                      break;
                      case "interval":
                     console.log(action.interval)
                     return {...state,...{interval:action.interval}};
                      break;
                      case "LyricARRObj":
                        //    console.log(action.interval)
                           return {...state,...{Lyric:action.LyricARRObj}};
                            break;
        default:
        return state;
        break;
    }
}