import React,{Component} from "react";
import { WingBlank, WhiteSpace } from 'antd-mobile';
import "./index.scss"

const SongSheetCenterObj = [
    {
        iconfont:"icon-pinglun",
        name:"评论",
        path:""
    },
    {
        iconfont:"icon-fenxiang",
        name:"分享",
        path:""
    },
    {
        iconfont:"icon-xiazai",
        name:"下载",
        path:""
    },
    {
        iconfont:"icon-duoxuan",
        name:"多选",
        path:""
    },
]

export default class SongSheetCenter extends React.Component{
    render(){
        const {
            title,
            coverImgUrl,
            backgroundUrl
        } = this.props
        return(
           
               <div className="SongSheetCenter">
                   <div className="SongSheetCenter-top">
                        <img src={coverImgUrl} alt=""/>
                        <div className="SongSheetCenter-top-right">
                            <div className="SongSheetCenter-name">
                                我喜欢的音乐
                            </div>
                            <div className="SongSheetCenter-img">
                                <img src={backgroundUrl} alt=""/>
                                <span>许蔡蔡</span>
                                <span className="iconfont icon-SanMiAppoutlinei14
"></span>
                            </div>
                        </div>
                   </div>
                   <div className="SongSheetCenter-button">
                        <ul>
                            {
                                SongSheetCenterObj.map((item,index)=>{
                                    return (
                                        <li>
                                            <span className={`iconfont ${item.iconfont}`}></span>
                                            <span>{item.name}</span>
                                        </li>
                                    )
                                })
                            }
                           
             
                        </ul>
                   </div>
               </div>
       
        )
    }
}