import React,{Component} from "react";
import { WingBlank, WhiteSpace } from 'antd-mobile';
import SongSheetTop from "../../components/SongSheetTop"
import SongSheetCenter from "../../components/SongSheetCenter"
import SongSheetDate from "../../components/SongSheetDate"
import "./index.scss"
import { axios } from "../../../utils";
export default class SongSheet extends React.Component{
    state = {
        id:'',
        coverImgUrl:'',
        backgroundUrl:''
    }
    componentDidMount(){
        // console.log(window.location.hash.split("=")[1])
        axios({
            url:`http://139.9.114.198:4000/playlist/detail?id=${window.location.hash.split("=")[1]}`,
            method:"GET"
        }).then(list=>{
            // console.log(list.data.playlist)
            // console.log(list.data.playlist.creator.backgroundUrl)
            this.setState({
                coverImgUrl:list.data.playlist.creator.avatarUrl,
                backgroundUrl:list.data.playlist.creator.backgroundUrl,
            })
        })
      
    }
    
    render(){
        return(
           <WingBlank>
               <div className="SongSheet">
                <div className="SongSheet-top" > 
                <SongSheetTop title="歌单"></SongSheetTop>
                <SongSheetCenter coverImgUrl={this.state.coverImgUrl} backgroundUrl={this.state.backgroundUrl}></SongSheetCenter>

                <div className="SongSheet-Thisearly" style={{background: `url("${this.state.coverImgUrl}") center center`,
    filter: 'blur(100px)'}}></div>
                </div>
                <SongSheetDate id={this.state.id}></SongSheetDate>
               </div>

          </WingBlank>
        )
    }
}