import React,{Component} from "react";
import { WingBlank, WhiteSpace } from 'antd-mobile';
import SongSheetTop from "../../components/SongSheetTop"
import "./index.scss"
import PlayerDate from "../../components/PlayerDate"
import PlayerRoll from "../../components/PlayerRoll"
import { axios } from "../../../utils";
import PlayerLi from "../../components/PlayerLi"
import {connect} from "react-redux"
import { Lyric,LyricTimesArr,Lyrics, indexs,interval,url ,urls , open , id , index , ListenMusicLiObjs , SongDate ,sourcePlay , totalTime , totalMinute ,totalTimeAlways , totalMinuteAlways , sourceAlways , formatAlways} from "../../redux/actions/index";
import EventEmitter from "events";  // Node 自带的模块

var myEvent = new EventEmitter();   //  emit on 
@connect(
    // 输入逻辑 mapStateToProps
    state=>{
        // console.log(state.open.open);
        return {
            SongDate:state.open.SongDate,
            id:state.open.id,
            sourcePlay:state.open.sourcePlay,
            // ...state,
            // value:state.data
        }
    },
    // 输出逻辑 mapDispatchToProps
    // (dispatch)=>{
    //     return {
    //         ListenMusicLiObj:()=>dispatch(ListenMusicLiObj()),
    //         // countDesc:()=>dispatch(countDesc),
    //         // change:num=>dispatch(changeCount(num)),
    //         // changeCity:city=>dispatch(changeCity(city))
    //     }
    // }
)

class Player extends React.Component{
    state = {
        picUrl:'',
        title:'',
        value:0,
        LyricsToArr:[],
        event:null,
        states:true
    }
    picUrl=(picUrl)=>{
        this.setState({
            picUrl:picUrl
        })
    }
    title=(title)=>{
        this.setState({
            title:title
        })
    }
    componentDidMount(){
        console.log()
        // console.log(this.props.id)
        // axios({
        //     url:"http://139.9.114.198:4000/lyric?id=569213220",
        //     method:"get"
        // }).then(list=>{
        //     console.log(list.data.lrc.lyric)
        //     // list.data.lrc.lyric
        //     this.props.dispatch(Lyric(list.data.lrc.lyric))
        //     console.log(this)
        // })
       
    }
    componentWillUpdate(){
    //    myEvent.on("changecountone",(err)=>{
    //         console.log(err)
    //     })
    }
    suber=(value)=>{
        // console.log(value)
        
        this.setState({
            value
        })
    }
    LyricsToArr=(LyricsToArr)=>{
        this.setState({
            LyricsToArr
        })
    }

    event=(event)=>{
        myEvent.emit("event",event)
    }
    Thisearly=()=>{
        // document.querySelector(".SongSheet-Thisearly").className = 'pause';
        console.log(this.props.sourcePlay)
        this.setState({
            states:!this.state.states
        })
        setTimeout(()=>{
            if(!this.state.states){
                this.Thisearlys.classList = "pause"
            }else{
                this.Thisearlys.classList = "pauses"
            }
        },1)
       
        
    }
    render(){
        // console.log(this.props)
        
        return(
            
            <div>
                           <PlayerLi  value={this.state.value} suber={this.suber} LyricsToArr={this.LyricsToArr} LyricsToArrObj={this.state.LyricsToArr} myEvent={myEvent}></PlayerLi>
                    
                              
            <div   className="SongSheet-Thisearly"  style={{background: `url("${this.state.picUrl}") center center`,
                filter: 'blur(100px)'}}></div>
                            <div className="Player">
                            <div className="Mask"></div>
                    <div className="SongSheet-lefts" style={{margin:'0 0.3rem 0 0.3rem'}}>
                        <SongSheetTop title={this.state.title}></SongSheetTop>
                        <PlayerRoll value={this.state.value} suber={this.suber} LyricsToArr={this.LyricsToArr} LyricsToArrObj={this.state.LyricsToArr} myEvent={myEvent}></PlayerRoll>
                        <div className="Player-top" onClick={()=>console.log(1)}>
                            <div className="Player-top-center">
                                <img src={this.state.picUrl} alt="" ref={el=>this.Thisearlys=el} />
                            </div>
                        </div>
                    </div>
                    
                    
                    <PlayerDate Thisearly={this.Thisearly}  event={this.event}   picUrl={this.picUrl} title={this.title} suber={this.suber} LyricsToArr={this.LyricsToArr} myEvent={myEvent}></PlayerDate>
                    {/* <PlayerDate picUrl={this.props.SongDate} title={this.title}></PlayerDate> */}
                    
                </div>
                
            </div>
               
           
           
        )
    }
}
export default  Player