import React,{Component} from "react";
import { NavBar, Icon } from 'antd-mobile';
import {history} from "../../../../src/utils/index"

export default class Head extends React.Component{
   
    render(){
        const {
            title
        } = this.props
        // console.log(this)
        return(
            <div className="Head">
            <NavBar
                mode="light"
                icon={<Icon type="left" />}
                onLeftClick={() => history.go(-1)}
        >{title}</NavBar>
            </div>
        )
    }
}